import fs from 'fs';
import path from 'path';

export type StoredFile = {
  name: string;
  description: string;
  encryptedData: string; // base64 or similar
};

const FILE_STORE_PATH = path.resolve(process.cwd(), 'file-store.json');

function readFileStore(): StoredFile[] {
  try {
    const data = fs.readFileSync(FILE_STORE_PATH, 'utf-8');
    return JSON.parse(data) as StoredFile[];
  } catch (err) {
    return [];
  }
}

function writeFileStore(files: StoredFile[]) {
  fs.writeFileSync(FILE_STORE_PATH, JSON.stringify(files, null, 2), 'utf-8');
}

export function getFiles(): StoredFile[] {
  return readFileStore();
}

export function addFile(file: StoredFile): boolean {
  const files = readFileStore();
  if (files.some(f => f.name === file.name)) {
    return false; // File with this name already exists
  }
  files.push(file);
  writeFileStore(files);
  return true;
}
